import React from "react"

function PaletteItem({ name, data, onSelected }) {
  return (
    <div className="flex flex-col shadow mr-2 mb-2 rounded-lg">
      <div className="flex">
        {Object.keys(data).map((weight, index) => {
          weight = parseInt(weight)
          let borderRadiusClass = ""
          if (weight === 100) {
            borderRadiusClass = " rounded-tl-lg"
          } else if (weight === 900) {
            borderRadiusClass = " rounded-tr-lg"
          }
          return (
            <div
              key={index}
              className={`w-8 h-8 ${borderRadiusClass}`}
              style={{
                backgroundColor: `var(--${name}-${weight})`,
              }}
            ></div>
          )
        })}
      </div>
      <div className="flex">
        <button className="py-1 px-3 text-gray-400">
          <i className="far fa-edit"></i>
        </button>
        <p className="py-1 flex-grow text-center text-sm">{name}</p>
        <div className="py-1 px-3 flex items-center">
          <input
            onChange={() => {
              onSelected(name)
            }}
            type="checkbox"
            className="form-checkbox"
          />
        </div>
      </div>
    </div>
  )
}

export default PaletteItem
