import React, { Component } from "react"
import "emmet-core"

const loadjs = require("loadjs")

class Editor extends Component {
  componentDidMount() {
    loadjs(
      [
        "https://cdnjs.cloudflare.com/ajax/libs/ace/1.4.6/ace.js",
        "https://cdnjs.cloudflare.com/ajax/libs/ace/1.4.6/ext-emmet.js",
        "https://cdnjs.cloudflare.com/ajax/libs/ace/1.4.6/ext-language_tools.js",
      ],
      {
        success: () => {
          const editor = window.ace.edit("editor", {
            mode: "ace/mode/html",
            theme: "ace/theme/chrome",
            wrap: true,
            enableEmmet: true,
            enableBasicAutocompletion: true,
            fontSize: 14,
            tabSize: 2,
          })
          editor.setValue(this.props.html, -1)
          editor.on("change", () => {
            this.props.onChange(editor.getValue())
          })
          const session = editor.getSession()
          // remove warning messages that are not needed
          session.on("changeAnnotation", function() {
            let annotations = session.getAnnotations() || []
            let len = annotations.length
            let i = annotations.length
            while (i--) {
              // warning about no doctype
              if (/doctype first\. Expected/.test(annotations[i].text)) {
                annotations.splice(i, 1)
              } else if (
                // warning when ampersand included inside URL
                /Named entity expected\. Got none\./.test(annotations[i].text)
              ) {
                annotations.splice(i, 1)
              }
            }
            if (len > annotations.length) {
              session.setAnnotations(annotations)
            }
          })
        },
        async: false,
      }
    )
  }

  render() {
    return <pre id="editor" style={{ height: "100%", width: "95%" }}></pre>
  }
}

export default Editor
