import React from "react"
import copy from "copy-to-clipboard"

const beautifyJs = require("js-beautify")

function ColorOutput({ selected, colors, onCopy }) {
  const extend = {}
  // const override = {}

  selected.forEach(className => {
    extend[className] = colors[className]
  })

  // eslint-disable-next-line
  const removeQuotesOnColorWeights = /\"(\d+)\":/g

  let output = `
  // tailwind.config.js
  module.exports = {
    theme: {
      extend: {
        colors: ${JSON.stringify(extend, null, 2).replace(
          removeQuotesOnColorWeights,
          "$1:"
        )}
      }
    }
  }`

  return (
    <div>
      <div className="mt-8">
        <h2>Output</h2>

        <div className="p-4 border mt-4 text-sm">
          <pre>
            <code className="language-javascript">
              {beautifyJs(output, {
                indent_size: 2,
              })}
            </code>
          </pre>
        </div>
        <button
          onClick={() => {
            // copy(output)
            // onCopy()
          }}
          type="button"
          className="px-6 py-3 border mt-4"
        >
          Copy to clipboard
        </button>
        {/* <p className="mt-4">{message}</p> */}
      </div>
    </div>
  )
}

export default ColorOutput
