import React, { Component } from "react"
import PaletteList from "./PaletteList"
// import SelectedPalettes from "./SelectedPalettes"
import Playground from "./Playground"
import ColorOutput from "./ColorOutput"
import Prism from "prismjs"
import "prismjs/themes/prism-okaidia.css"
const colors = require("../../colors")

class PalettesContainer extends Component {
  constructor(props) {
    super(props)

    this.state = {
      colors: colors,
      selected: ["gray-1", "blue-1"],
    }
  }

  componentDidMount() {
    Prism.highlightAll()
  }

  handlePaletteSelected = className => {
    console.log(className)
    // if within list then remove, else add
    let newSelection = []
    if (this.state.selected.includes(className)) {
      newSelection = this.state.selected.filter(name => name !== className)
    } else {
      newSelection = [className, ...this.state.selected]
    }

    this.setState({
      selected: newSelection,
    })
    Prism.highlightAll()
  }

  render() {
    return (
      <div>
        <div className="mt-8">
          <PaletteList
            colors={this.state.colors}
            onSelected={this.handlePaletteSelected}
          />
          {/* <div className="mt-4">
            <SelectedPalettes
              selected={this.state.selected}
              colors={this.state.colors}
            />
          </div> */}
        </div>
        <div className="mt-8">
          <Playground />
        </div>
        <div className="mt-8">
          <h2>Import into Tailwind CSS</h2>
          <ColorOutput
            selected={this.state.selected}
            colors={this.state.colors}
          />
        </div>
      </div>
    )
  }
}

export default PalettesContainer
