import React from "react"
import PaletteItem from "./PaletteItem"

function PaletteList({ colors, onSelected }) {
  return (
    <div>
      <h2>All Palettes</h2>
      <div className="overflow-auto" style={{ maxHeight: "13.3125rem" }}>
        {Object.entries(colors).map(([colorName, colorData], index) => {
          return (
            <div key={index} className="palette-row inline-block">
              <div>
                <PaletteItem
                  name={colorName}
                  data={colorData}
                  onSelected={onSelected}
                />
              </div>
              <div></div>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default PaletteList
